.shoppingBag{
    width: 45px;
    height:45px;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;

    .shoppingBagIcon{
        width: 24px;
        height: 24px;
    }

    .itemCount{
        position: absolute;
        font-size: 10px;
        font-weight: bold;
        bottom: 12px;
    }
}